import Typed from 'typed.js';
import { TypeAnimation } from 'react-type-animation';
import myphoto from '../Components/assets/myphoto.png'
const TopSection =()=>{




  return (
 <div className="wrapper">
    <div className="cols cols0">
<span className="topline">Hello</span>
<div className='words'>

<h3 className="iam">I am a<span className="multitext">
<TypeAnimation
sequence={[
  'Coder', // Types 'One'
  1000, // Waits 1s
  'Software Engineer', // Deletes 'One' and types 'Two'
  2000, // Waits 2s
  'Web Developer',
  1000 // Types 'Three' without deleting 'Two'
  //   () => {
    //     console.log('Done typing!'); // Place optional callbacks anywhere in the array
    //   }
  ]}
  wrapper="span"
  cursor={true}
  repeat={Infinity}
  style={{ fontSize: '0.8em' }}
/></span></h3>
  </div>
<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laboriosam facilis sequi et sunt, similique voluptas?</p>
   <div className="btns">
    <button>Download CV</button>
    <button>Hire me</button>
   </div>
   
    </div>

<div className="cols cols1"></div>


<div className="imagebox">
    <img width={"200px"} src="https://previews.123rf.com/images/nottomanv1/nottomanv11610/nottomanv1161000390/65646095-abstract-purple-watercolor-on-white-background-this-is-watercolor-splash-it-is-drawn-by-hand-.jpg" id="splash" alt="" />
    <img width={"800px"} src={myphoto}  alt="" />
</div>

</div>
)}
  
export default TopSection